<template><section>
<h1>Icon 图标</h1>
<h2>使用方法</h2>
<p>设置类名为 vi-icon-iconName 即可</p>
<div class="icon-page demo-block">
  <p>
    <i class="vi-icon-edit"></i>
    <i class="vi-icon-female"></i>
    <i class="vi-icon-spinner"></i>
    <i class="vi-icon-spinner2"></i>
  </p>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div class="demo-block">
  <p>
    <i class="vi-icon-edit"></i>
    <i class="vi-icon-female"></i>
    <i class="vi-icon-spinner"></i>
    <i class="vi-icon-spinner2"></i>
  </p>
</div>

</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"vi-icon-edit"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"vi-icon-female"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"vi-icon-spinner"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"vi-icon-spinner2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span> &gt;</span>

</code></pre>
</div></demo-block>
<h2>图标集合</h2>
<!-- 动态插入所有icon -->
<div id="allIcons"></div> 
</section></template>
<style>
#allIcons {
  /* display: flex;
  flex-wrap: wrap; */
  overflow: hidden;
}
#allIcons .icon-item {
  /* flex: 1 0 150px; */
  float: left;
  padding: 20px;
}
#allIcons i,  .icon-page i{
  display: inline-block;
  padding: 20px;
  font-size: 40px;
}
</style>
<script type="text/javascript">
var iconsInfo = window.iconsInfo;
var prefix = iconsInfo.preferences.fontPref.prefix;
function createIconItem(icon) {
  var className = prefix + icon.properties.name;
  var wrapper = document.createElement('div');
  wrapper.className = 'icon-item'
  var iEle = document.createElement('i');
  iEle.className = className;
  var pEle = document.createElement('p');
  pEle.textContent = className
  wrapper.appendChild(iEle)
  wrapper.appendChild(pEle)
  return wrapper;
}
var timer = setInterval(function() { // 暂时先这么干，以后实现直接生成本文件（或者nodejs直接把内容插入到这里）
  var parent = document.getElementById('allIcons');
  if(parent && parent.children.length <= 0) {
    iconsInfo.icons.forEach(function(icon) {
      parent.append(createIconItem(icon))
    })
  }
}, 1000)
</script>


